import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";
import ButtonBack from "../components/elements/backButton";


export default function BlogSingle({post}) {
    const {t} = useTranslation();

    return (
        <div className="main-wrapper min-h-screen pt-36 pb-20">
            <div className="container mx-auto mt-4 lg:mt-18">
                <div className="absolute top-12">
                    <Link to="/blogs/">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </div>
                <div className="grid grid-cols-1 gap-20 lg:grid-cols-5 mt-10">
                    <div className="lg:col-span-3 flex items-center lg:order-1 order-2 mt-12 lg:mt-0">
                        <div>
                            <h1 className="text-3xl md:text-5xl text-white font-bold">
                                {post.title}
                            </h1>
                            <div className="text-white mt-16" dangerouslySetInnerHTML={{__html: post.content}}/>

                        </div>
                    </div>
                    <div className="lg:col-span-2 mt-12 lg:mt-0 order-1 lg:order-2">
                        <Img className="object-cover h-full w-full" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                    </div>
                </div>
            </div>
        </div>
    )
}
