import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image"
import {useTranslation} from "react-i18next";
import ButtonBack from "../components/elements/backButton";


export default function ProjectSingle({post}) {
    const {t} = useTranslation();

    function capitalizeFirstLetter(str) {
        return str[0].toUpperCase() + str.slice(1);
    }

    return (
        <div className="main-wrapper pt-36 pb-20">
            <div className="container mx-auto mt-4 lg:mt-18">
                <div className="absolute top-12">
                    <Link to="/portfolio/">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </div>
                <div className="grid grid-cols-1 lg:grid-cols-5 mt-10">
                    <div className="lg:col-span-2 flex flex-col justify-between">
                        <div/>
                        <div>
                            <h1 className="text-4xl text-white font-bold">
                                {post.title}
                            </h1>
                        </div>
                        <div className="pt-4 pb-2 flex flex-row">
                            {post.tags.nodes.map((tag, i) => (
                                <span className="text-center text-white mr-5" key={i}>
                                                    <img src={`/img/develop_stack/${tag.name}.svg`} style={{height: "40px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                                    {capitalizeFirstLetter(tag.name)}
                                                </span>))}
                        </div>
                    </div>
                    <div className="lg:col-span-3 mt-12 lg:mt-0">
                        <Img className="object-cover h-full w-full" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Project image"/>
                    </div>
                </div>

                <div className="flex justify-center">
                    <div className="mt-16 text-white lg:w-2/4 px-4 lg:px-0" dangerouslySetInnerHTML={{__html: post.content}}/>
                </div>
            </div>
        </div>
    )
}
