import React from "react"
import {graphql} from "gatsby"
import BlogSingle from "./blogs_single";
import ProjectSingle from "./projects_single";
import PartnerSingle from "./partners_single";
import Navigation from "../components/Navigation";


export default function BlogPost({data}) {
    const post = data.allWpPost.nodes[0]
    return (
        <>

            {post.categories.nodes[0].name === 'blogs' &&
            <>
                <Navigation isPost={true} pageNr={'05'} post={post}/>

                <BlogSingle post={post}/>
            </>

            }
            {post.categories.nodes[0].name === 'projects' &&
            <>
                <Navigation isPost={true} pageNr={'04'} post={post}/>

                <ProjectSingle post={post}/>
            </>
            }
            {post.categories.nodes[0].name === 'partners' &&
            <>
                <Navigation isPost={true} pageNr={'03'} post={post}/>

                <PartnerSingle post={post}/>
            </>
            }
        </>
    )
}
export const query = graphql`
  query($slug: String!) {
    allWpPost(filter: { slug: { eq: $slug } }) {
      nodes {
        categories {
            nodes {
             name
            }
        }
        excerpt
        title
        content
        slug
        tags {
              nodes {
                name
              }
            }
        featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fluid(maxWidth: 2000, quality: 100) {
                         ...GatsbyImageSharpFluid
                     }
                     fixed(width: 200, quality: 100) {
                         ...GatsbyImageSharpFixed
                     }
                   }
                 }
              }
            }
      }
    }
  }
`
